import React, {useState, useEffect} from 'react';
import AppCard from '../../../@crema/core/AppCard';
import {Box} from '@mui/material';
import {Fonts} from '../../../shared/constants/AppEnums';
import Divider from '@mui/material/Divider';
import {useIntl} from 'react-intl';
import AppAnimate from '../../../@crema/core/AppAnimate';
import PropTypes from 'prop-types';
import TextField from '@mui/material/TextField';
import {useSelector} from 'react-redux';

// id: 156
// name: "Tie-detail top"
// price: 50
// qty: 1
// sale_price: 50
// slug: "tie-detail-top"
// sm_pictures: (3) [{…}, {…}, {…}]
// sum: 50
const getTotalPrice = (cartItems) => {
  let total = 0;
  cartItems.map((data) => {
    total =
      total +
      (data.sale_price ? data.sale_price.toFixed(2) : data.price.toFixed(2)) *
        +data.qty;
    return data;
  });
  return total;
};

const OrderSummary = ({cartItems}) => {
  const [shippingValue, setshippingValue] = useState(10);
  const {messages} = useIntl();
  const totalPrice = getTotalPrice(cartItems);
  const [discouontValue, setdiscouontValue] = useState(0);
  const [orderTotal, setorderTotal] = useState(totalPrice + shippingValue);
  // console.log(cartItems);
  useEffect(() => {
    setorderTotal(totalPrice + Number(shippingValue) - Number(discouontValue));

    let prod = {
      shippingValue: Number(shippingValue),
      discouontValue: Number(discouontValue),
      Price: totalPrice,
      totalOrderProce:
        totalPrice + Number(shippingValue) - Number(discouontValue),
    };
    window.sessionStorage.setItem('orderPrice', JSON.stringify(prod));
  }, [
    shippingValue,
    discouontValue,
    useSelector(({ecommerce}) => ecommerce.cartItems),
  ]);
  return (
    <AppAnimate animation='transition.slideUpIn' delay={200}>
      <AppCard
        title={
          <Box fontSize={16} fontWeight={Fonts.BOLD}>
            {messages['ecommerce.orderSummary']}
          </Box>
        }
      >
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mt: 2,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['invoice.grandTotal']}: </Box>
          <Box>${totalPrice}</Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['ecommerce.descont']}: </Box>
          <Box>
            <TextField
              value={discouontValue}
              onChange={(e) => setdiscouontValue(e.target.value)}
              name='numberformat'
              id='formatted-numberformat-input'
              autoFocus={true}
              type='number'
              variant='standard'
            />
          </Box>
        </Box>
        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            mb: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['ShippingCharge']}: </Box>
          <Box>
            <TextField
              value={shippingValue}
              onChange={(e) => setshippingValue(e.target.value)}
              name='numberformat'
              id='formatted-numberformat-input'
              autoFocus={true}
              type='number'
              variant='standard'
            />
          </Box>
        </Box>

        <Divider />

        <Box
          sx={{
            display: 'flex',
            justifyContent: 'space-between',
            fontSize: 14,
            fontWeight: Fonts.MEDIUM,
            my: 4,
          }}
        >
          <Box sx={{color: 'text.secondary'}}>{messages['OrderTotal']}: </Box>
          <Box>${orderTotal}</Box>
        </Box>
      </AppCard>
    </AppAnimate>
  );
};

export default OrderSummary;

OrderSummary.propTypes = {
  cartItems: PropTypes.array,
};
